* {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff8ec;
}

main {
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.profile {
  flex-direction: row;
  align-items: center;
  margin: 1rem;
  padding-left: .5rem;
  display: none;
}

h1, h2, h3, h4, h5, .buttons, .copyright, .form-ui, #navbar a {
  font-family: Inknut Antiqua, Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
}

h1 {
  color: #b59136;
  letter-spacing: 1px;
  margin: 1rem;
  font-size: 2.75rem;
}

h2 {
  color: #005b4c;
  font-size: 1.8rem;
}

h4 {
  color: #005b4c;
}

.row {
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.col {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

#header-data {
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.fa-user-astronaut {
  color: #005b4c;
  cursor: pointer;
  border: 3px solid #005b4c;
  border-radius: 50%;
  padding: .35rem;
  font-size: 1.5rem;
}

.user {
  margin: 1.5rem 1rem;
}

.logo {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

#logout-btn {
  display: none;
}

#logo-png {
  width: 6rem;
  height: 6rem;
}

form {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.form-wrapper {
  width: 48rem;
  background-color: #fff;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  margin: 1rem;
  padding: 2rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.account-title {
