}

input {
  height: 2rem;
  border: 2px solid #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  padding: .5rem 1.5rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.inputs-div {
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.input-login {
