  background-color: #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  font-size: 1.06rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.buttons:hover {
  background-color: #604200;
}

#forums-section {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
}

#navbar {
  width: 80%;
  border-bottom: 2px solid #604200;
  flex-direction: row;
  justify-content: center;
  margin: 1rem;
  display: flex;
}

#navbar a {
  color: #b59136;
  width: 25%;
  border-top: 2px solid #b59136;
  border-right: 2px solid #b59136;
  border-top-right-radius: .5rem;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  text-decoration: none;
  display: flex;
}

#navbar a:hover {
  color: #604200;
  background-color: #ffeeca;
  border-top: 2px solid #604200;
  border-right: 2px solid #604200;
}

#navbar a.active {
  color: #604200;
  background-color: #ffeeca;
}

#travel-forum {
  border-left: 2px solid #b59136;
  border-top-left-radius: .5rem;
}

#travel-forum:hover {
  border-left: 2px solid #604200;
}

.marg-right {
  margin-right: .5rem;
}

#topics-section {
  width: 85%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#travel-div {
  display: flex;
}

#sport-div, #gaming-div {
  display: none;
}

.topics {
  width: 80%;
  min-height: 30rem;
  background-color: #fff;
  border: 2px solid #005b4c;
  border-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin: 1rem 1rem 3rem;
  padding: 1rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.chat-container {
  width: 100%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.message-container {
  width: 80%;
  border: 2px solid #005b4c;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: space-between;
  margin: .25rem;
  padding: .5rem 1.2rem;
