  box-shadow: 0 3px 8px #4a4453b3;
}

.message-container:hover {
  background-color: #ffeeca;
  border-color: #766031;
}

.user-div {
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.mess-div {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 0 1rem;
  display: flex;
}

.delete-btn {
  color: #fff;
  width: 1.5rem;
  height: 1.5rem;
  background-color: #c1564b;
  border-radius: .4rem;
  padding: .3rem;
  font-size: .7rem;
  font-weight: bold;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.delete-btn:hover {
