}

.mess-edit:focus {
  background-color: #fff;
  border: 2px solid #ff614c;
  border-radius: .25rem;
  padding: .25rem .5rem;
  font-style: italic;
  font-weight: bold;
}

.mess-show-profile {
  cursor: pointer;
}

.mess-show-profile:hover {
  color: #ff614c;
}

.footer {
  width: 100%;
  height: 4.2rem;
  background-color: #b59136;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-top: 1rem;
  font-size: 1.1rem;
  display: flex;
  box-shadow: 4px 0 8px #4a4453b3;
}

.copyright {
  color: #fff;
  padding: .5rem;
}

#chevron a {
  color: #fff;
  font-size: 2.3rem;
}

#chevron a:hover {
  text-shadow: 2px 3px 5px #4a4453b3;
}

.modal-background {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  padding-top: 6.25rem;
  padding-left: 1.875rem;
  padding-right: 1.875rem;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-container {
  max-width: 35rem;
  background-color: #fefefe;
  border: 2px solid #604200;
  border-radius: .5rem;
  margin: auto;
  padding-bottom: 1rem;
  animation-name: animatetop;
  animation-duration: .4s;
  position: relative;
  box-shadow: 4px 0 8px #4a4453b3;
}

textarea {
  resize: none;
  border: 2px solid #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  padding: .5rem 1.5rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.img-container {
  width: 30rem;
  height: 5rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile-img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.profile-img:hover {
  text-shadow: 3px 3px 5px #b59136;
  border-bottom: 5px solid #604200;
}

.selected-profile-img {
  width: 5rem;
  height: 5rem;
  border-bottom: 6px solid #898376;
  border-radius: 50%;
  margin-right: 1rem;
}

@-webkit-keyframes animatetop {
  from {
    opacity: 0;
    top: -18.75rem;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

@keyframes animatetop {
  from {
    opacity: 0;
    top: -300px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

.modal-header {
  color: #fff;
  background-color: #b59136;
  border-top-left-radius: .5rem;
  border-top-right-radius: .5rem;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: .625rem 1.25rem;
