}

.close {
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.container-user-modal {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
}

#profil-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 1rem 2rem;
  display: flex;
}

